.banner {
    padding-top: 125px;
    padding-bottom: 118px;
    background-image: url("../img/banner.png");
    text-align: center;
    @media #{$tablet} {
        padding-top: 60px;
    }
    &__title {
        color: $white;
        font-family: 'Roboto', sans-serif;
        font-size: 40px;
        font-weight: 400;
        @media #{$tablet} {
            font-size: 35px;
        }
        @media #{$mobile} {
            font-size: 20px;
        }
    }
}
.appointment {
    padding-top: 85px;
    @media #{$mobile} {
        padding-top: 60px;
    }
    &__row {
        display: flex;
        justify-content: center;
        padding-bottom: 40px;
        padding-left: 18px;
        &:first-child {
            padding-bottom: 57px;
            padding-left: 16px;
            @media #{$tablet} {
                padding-left: 0;
                padding-bottom: 0;
            }
            @media #{$mobile} {
                padding-left: 0;
                padding-bottom: 0;
            }
        }
        @media #{$tablet} {
            flex-wrap: wrap;
            padding-left: 0;
            padding-bottom: 0;
        }
        @media #{$mobile} {
            display: block;
            padding-left: 0;
            padding-bottom: 0;
        }
    }
}
.form {
    &__item {
        position: relative;
        margin: 0 5px;
        width: 22.9%;
        height: 45px;
        border-radius: 5px;
        background-color: $white;
        @media #{$tablet} {
            margin: 13px 5px;
            width: 46%;
        }
        @media #{$mobile} {
            width: 100%;
            margin: 25px 0;
        }
        .info {
            &__textfield {
                line-height: 45px;
                padding: 0 16px;
                width: 100%;
                border-radius: 5px;
                font-size: 18px;
                font-weight: 400;
                color: $grey;
                font-family: 'Roboto', sans-serif;
                &::placeholder {
                    color: $grey;
                }
                @media #{$tablet} {
                    font-size: 12px;
                }

            }
        }
    }
    &__btn {
        margin-left: -15px;
        border-radius: 5px;
        background-color: #ff8e41;
        font-size: 20px;
        font-weight: 700;
        line-height: 24px;
        color: $white;
        width: 29%;
        height: 70px;
        &:hover {
            color: $orange;
            background-color: $white;
            border: 1px solid $orange;
        }
        @media #{$tablet} {
            width: 70%;
        }
        @media #{$mobile} {
            width: 70%;
        }
    }
}
.select {
    position: relative;
    width: 100%;
    &__title {
        position: absolute;
        top: -25px;
        left: 20px;
        color: $grey-blue;
        font-size: 14px;
        font-weight: 400;
        line-height: 24px;
        @media #{$tablet} {
            top: -23px;
            left: 11px;
            font-size: 11px;
        }
    }
    &__specs,
    &__service,
    &__date,
    &__time {
        width: 100%;
        height: 45px;
        border-radius: 5px;
        border: none;
        padding-left: 15px;
        font-size: 18px;
        font-weight: 400;
        line-height: 24px;
        option {
            font-size: 14px;
            font-weight: 400;
            line-height: 28px;
            outline: none;
            border: none;
        }
    }
    &__date {
        color: $grey;
    }
}


